.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.corona-total {
    background-color: rgba(255, 255, 255, 0.1);
    color: #fff;
    z-index: 1;
    position: absolute;
    top: 10px;
    right: 10px;
    border-radius: 4px;
    border: 2px solid #fff;
}

.total-deaths {
    background: rgba(255, 99, 132, 0.8);
    padding: 0.5rem 1rem 0.5rem 1rem;
}

.total-confirmed {
    background: rgba(192, 169, 110, 0.8);
    padding: 0.5rem 1rem 0.5rem 1rem;
}

.total-cured {
    background: rgba(11, 123, 59, 0.8);
    padding: 0.5rem 1rem 0.5rem 1rem;
}

#tooltip {
    position: absolute;
    z-index: 1;
    pointer-events: none;
    border-radius: 5px;
    border: 1px solid #fff;
    right: 1rem;
    top: 1rem;
    background: rgba(0, 0, 0, 0.7);
}

.map-corona {
    position: relative;
    width: 100%;
    height: 80vh;
    box-shadow: 0 3px 4px rgba(0, 0, 0, .7);
}

.info {
    width: 70vw;
    margin: 1rem auto;
}

#app {
    font-family: 'Roboto', sans-serif;
    color: #262C31;
}

h1 {
    margin-top: 2rem;
    font-family: 'Roboto Slab', serif;
}

.info-data-header {
    display: inline-block;
    margin: 1rem 0 .5rem;
    font-family: 'Roboto Slab', serif;
}

.info-data-info{
    border-bottom: 1px dashed #262C31;
    padding-bottom: 1rem;
}
